:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --gray: hsl(0, 0%, 55%);
  --darkgray: hsl(0, 0%, 41%);
  --light-white: hsla(0, 0%, 100%, 0.75);
}

:root {
  --font-alata: "Alata", sans-serif;
  --font-josefin: "Josefin Sans", sans-serif;
}

.container {
  max-width: 100%;
  margin: 0 auto 9.6rem;
  position: relative;
}
@media (min-width: 37.5em) {
  .container {
    width: 60rem;
  }
}
@media (min-width: 48em) {
  .container {
    width: 72rem;
  }
}
@media (min-width: 64em) {
  .container {
    width: 96rem;
    margin: 0 auto 18.4rem;
  }
}
@media (min-width: 75em) {
  .container {
    width: 110rem;
  }
}

body {
  font-size: 1.5rem;
}

html {
  font-size: 62.5%;
}

.heading-primary {
  font-size: 4rem;
  font-family: "Josefin Sans";
  color: var(--white);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-transform: uppercase;
  border: 2px solid var(--white);
  padding: 2.4rem;
  padding-top: 2.6rem;
}
.heading-primary span {
  display: block;
}
@media (min-width: 48em) {
  .heading-primary {
    font-size: 5.6rem;
    left: 0;
    transform: translate(0, -50%);
    padding: 4rem 8rem 4rem 4rem;
  }
}
@media (min-width: 75em) {
  .heading-primary {
    font-size: 7.2rem;
  }
}

.heading-secondary {
  font-size: 3.2rem;
  font-family: "Josefin Sans";
  text-transform: uppercase;
}
.heading-secondary span {
  display: block;
}
@media (min-width: 48em) {
  .heading-secondary {
    font-size: 4rem;
  }
}
@media (min-width: 75em) {
  .heading-secondary {
    font-size: 4.8rem;
  }
}

.heading-tertiary {
  font-size: 2.4rem;
  color: var(--black);
  text-transform: uppercase;
}
.heading-tertiary span {
  display: block;
}
@media (min-width: 48em) {
  .heading-tertiary {
    font-size: 3.2rem;
  }
}

.button {
  display: inline-block;
  padding: 1.3rem 4rem;
  border: 1px solid var(--black);
  text-transform: uppercase;
  font-size: 1.4rem;
  font-family: "Alata";
  cursor: pointer;
  transition: 0.4s;
  letter-spacing: 0.5rem;
}
.button:hover {
  background-color: var(--black);
  color: var(--white);
}

.header {
  height: 65rem;
  padding: 0 2.4rem;
  margin-bottom: 9.6rem;
  background-image: url(../../../images/mobile/image-hero.jpg);
  background-size: cover;
  background-position: center center;
}
@media (min-width: 48em) {
  .header {
    background-image: url(../../../images/desktop/image-hero.jpg);
  }
}
@media (min-width: 64em) {
  .header {
    margin-bottom: 16rem;
  }
}
.header__container {
  margin: 0 auto;
  position: relative;
  height: 100%;
}
@media (min-width: 37.5em) {
  .header__container {
    max-width: 60rem;
  }
}
@media (min-width: 48em) {
  .header__container {
    max-width: 72rem;
  }
}
@media (min-width: 64em) {
  .header__container {
    max-width: 96rem;
  }
}
@media (min-width: 75em) {
  .header__container {
    max-width: 110rem;
  }
}

.nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.nav__logo {
  position: absolute;
  top: 3.6rem;
  left: 0;
  z-index: 11;
}
.nav__menu {
  position: absolute;
  margin: 0 calc(50% - 50vw);
  width: 100vw;
  height: 100%;
  background-color: var(--black);
  top: -150%;
  left: 0;
  z-index: 10;
  color: var(--white);
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 2.4rem;
  transition: 0.4s;
}
@media (min-width: 48em) {
  .nav__menu {
    top: 4.6rem;
    background-color: transparent;
    flex-direction: row;
    height: auto;
    justify-content: end;
    padding-left: 0;
    right: 0;
    margin: 0;
    width: 100%;
  }
}
.nav__list {
  display: flex;
  flex-direction: column;
  row-gap: 2.4rem;
}
@media (min-width: 48em) {
  .nav__list {
    flex-direction: row;
    column-gap: 3.2rem;
  }
}
.nav__link {
  text-transform: uppercase;
  font-size: 2.4rem;
  display: inline-block;
  font-family: var(--font-josefin);
  position: relative;
}
@media (min-width: 48em) {
  .nav__link {
    text-transform: initial;
    font-size: 1.5rem;
  }
}
.nav__link::after {
  content: "";
  position: absolute;
  left: 50%;
  bottom: -8px;
  width: 24px;
  height: 2px;
  background-color: var(--white);
  transform: scale(0, 1) translateX(-50%);
  transform-origin: left top;
  transition: transform 0.4s;
}
.nav__link:hover::after {
  transform: scale(1, 1) translateX(-50%);
}
.nav__toggle {
  position: absolute;
  top: 4.4rem;
  right: 0;
  cursor: pointer;
}
@media (min-width: 48em) {
  .nav__toggle {
    display: none;
  }
}
.nav__close {
  position: absolute;
  top: 4.2rem;
  right: 2.6rem;
  z-index: 10;
  cursor: pointer;
}
@media (min-width: 48em) {
  .nav__close {
    display: none;
  }
}

.show-menu {
  top: 0;
}

.brand {
  padding: 0 2.4rem;
  text-align: center;
}
.brand__content {
  margin-top: 4.8rem;
}
@media (min-width: 64em) {
  .brand__content {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 54rem;
    margin-top: 0;
    padding-top: 9.6rem;
    padding-left: 9.6rem;
    background-color: var(--white);
  }
}
.brand__img {
  width: 100%;
}
@media (min-width: 64em) {
  .brand__img {
    width: auto;
  }
}
.brand__description {
  font-family: var(--font-alata);
  color: var(--gray);
  line-height: 1.7;
  margin-top: 1.6rem;
}

.creation {
  padding: 0 2.4rem;
}
.creation__wrapper {
  display: grid;
}
@media (min-width: 64em) {
  .creation__wrapper {
    grid-template-columns: repeat(2, 1fr);
  }
}
.creation__container {
  display: grid;
  row-gap: 2.4rem;
  margin-top: 4.8rem;
  margin-bottom: 3.4rem;
}
@media (min-width: 48em) {
  .creation__container {
    grid-template-columns: repeat(2, 1fr);
    column-gap: 3rem;
    row-gap: 3rem;
  }
}
@media (min-width: 64em) {
  .creation__container {
    grid-template-columns: repeat(4, 1fr);
    grid-column: 1/3;
    margin-bottom: 0;
  }
}
.creation__title {
  justify-self: center;
}
@media (min-width: 64em) {
  .creation__title {
    grid-row: 1/2;
    align-self: center;
    justify-self: start;
  }
}
.creation__content {
  display: block;
  position: relative;
  cursor: pointer;
}
.creation__content:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 48em) {
  .creation__content {
    height: 42rem;
  }
}
.creation__content-title {
  position: absolute;
  bottom: 2rem;
  left: 2rem;
  color: var(--white);
  font-family: var(--font-josefin);
  z-index: 10;
}
.creation__content::after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(to left, rgba(0, 0, 0, 0.01), rgba(0, 0, 0, 0.6));
  transition: 0.4s;
  z-index: 9;
}
.creation__content:hover .creation__content-title {
  color: var(--black);
}
.creation__content:hover::after {
  background-image: none;
  background-color: var(--light-white);
}
.creation__img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}
.creation__button {
  grid-column: 1/-1;
  justify-self: center;
}
@media (min-width: 64em) {
  .creation__button {
    grid-row: 1/2;
    grid-column: 2/-1;
    justify-self: end;
    align-self: center;
  }
}

.footer {
  font-family: var(--font-alata);
  color: var(--white);
  padding: 5.6rem 0;
  background-color: var(--black);
}
.footer__container {
  display: grid;
  justify-content: center;
  margin-bottom: 0;
}
@media (min-width: 64em) {
  .footer__container {
    grid-template-columns: repeat(2, 1fr);
    justify-content: start;
    row-gap: 1.4rem;
  }
}
.footer__logo {
  justify-self: center;
}
@media (min-width: 64em) {
  .footer__logo {
    justify-self: start;
  }
}
.footer__nav {
  justify-self: center;
  margin-top: 3.2rem;
  margin-bottom: 4.8rem;
}
@media (min-width: 64em) {
  .footer__nav {
    margin: 0;
    grid-column: 1/2;
    grid-row: 2/3;
    justify-self: start;
    align-self: center;
  }
}
.footer__nav-list {
  display: flex;
  flex-direction: column;
}
@media (min-width: 64em) {
  .footer__nav-list {
    flex-direction: row;
    column-gap: 3.2rem;
  }
}
.footer__nav-item {
  margin-bottom: 1.6rem;
}
@media (min-width: 64em) {
  .footer__nav-item {
    margin-bottom: 0;
  }
}
.footer__nav-item:last-of-type {
  margin-bottom: 0;
}
.footer__nav-link {
  font-size: 1.5rem;
  text-transform: initial;
}
.footer__share {
  justify-self: center;
  display: flex;
  align-items: center;
  margin-bottom: 1.6rem;
  column-gap: 1.6rem;
}
@media (min-width: 64em) {
  .footer__share {
    margin-bottom: 0;
    justify-self: end;
    align-self: center;
  }
}
.footer__link {
  display: inline-block;
  position: relative;
}
.footer__link::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -8px;
  width: 100%;
  height: 2px;
  background-color: var(--white);
  transform: scale(0, 1);
  transform-origin: left top;
  transition: transform 0.4s;
}
.footer__link:hover::after {
  transform: scale(1, 1);
}
.footer__icon {
  margin-right: 1.6rem;
}
.footer__icon:last-of-type {
  margin-right: 0;
}
.footer__copyright {
  color: var(--gray);
}
@media (min-width: 64em) {
  .footer__copyright {
    justify-self: end;
    align-self: end;
  }
}